.button {
    border-radius: 4px;
    text-align: center;
    font-size: 20px;
    padding: 15px;
    width: 200px;
    background-color: rgb(2, 2, 38);
    color: white;
    border: none;
    transition: all 0.7s;
    cursor: pointer;
    display:block;
    float: right;
    margin-top: 50px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

  }
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
 
  .button:hover {
    background-color: white;
    color: black;
  }

